//
// _timeline.scss
//

//activity-timeline
.acitivity-timeline {
    position: relative;
    overflow: hidden;

    .acitivity-item {
        position: relative;

        .flex-shrink-0 {
            z-index: 2;
        }

        .acitivity-avatar {
            background-color: var(--#{$prefix}secondary-bg);
            border: 3px solid var(--#{$prefix}secondary-bg);
            height: 32px;
            width: 32px;
        }

        &:before {
            content: "";
            position: absolute;
            border-left: 1px dashed var(--#{$prefix}border-color);
            left: 16px;
            height: 100%;
            top: 5px;
            z-index: 0;
        }

        &:last-child {
            &::before {
                border-color: transparent;
            }
        }
    }
}

.acitivity-timeline-2 {
    border-radius: 12px;
    position: relative;

    li {
        padding-bottom: 1.5rem;
        border-left: 1px solid #abaaed;
        position: relative;
        padding-left: 20px;
        margin-left: 10px;

        &:last-child {
            border: 0px;
            padding-bottom: 0;
        }

        &:before {
            content: '';
            width: 15px;
            height: 15px;
            background: var(--#{$prefix}secondary-bg);
            border: 1px solid var(--#{$prefix}primary);
            box-shadow: 3px 3px 0px lighten($primary, 30%);
            box-shadow: 3px 3px 0px lighten($primary, 30%);
            border-radius: 50%;
            position: absolute;
            left: -8px;
            top: 0px;
        }
    }
}


// vertical sitemap
.verti-sitemap {
    a {
        color: var(--#{$prefix}body-color);
        display: block;
    }

    .parent-title {
        a {
            padding-left: 0;

            &:before {
                display: none;
            }
        }

        &:before {
            display: none;
        }
    }

    .first-list {
        position: relative;
        padding-top: 10px;

        &:before {
            content: "";
            border-left: 2px dashed $border-color;
            position: absolute;
            top: 0;
            height: 100%;
            bottom: 0;
            left: 0;
        }

        .list-wrap,
        li {
            a {
                position: relative;
                padding: 10px 16px 4px 36px;

                &::before {
                    content: "";
                    width: 24px;
                    border-top: 2px dashed $border-color;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                }
            }
        }

        .second-list,
        .third-list {
            margin-left: 42px;

        }

        .third-list,
        .second-list {
            position: relative;

            li {
                position: relative;

                &:before {
                    content: "";
                    height: 100%;
                    border-left: 2px dashed $border-color;
                    position: absolute;
                    top: 0;
                    left: 0;
                    margin: 0px auto;
                }

                &:last-child {
                    &::before {
                        height: 13px;
                    }
                }
            }
        }

        &:last-child {
            &::before {
                height: 25px;
            }
        }
    }
}